import React, { PureComponent } from 'react'

import Navbar from 'react-bootstrap/Navbar'

import Badge from 'react-bootstrap/Badge'
import ButtonToolbar from 'react-bootstrap/ButtonToolbar'

import Form from 'react-bootstrap/Form'
import FormControl from 'react-bootstrap/FormControl'

// import classifierLevel from '../helper/classifierLevel'
// import classifierNumber from '../helper/classifierNumber'
// import LevelColor from '../helper/colors'

import SettingsModal from './Settings'
import { addUrlProps, UrlQueryParamTypes, configureUrlQuery } from 'react-url-query' // https://github.com/pbeshai/react-url-query

import { createBrowserHistory } from 'history'

import { compose } from 'redux'
import { connect } from 'react-redux'
import { update } from '../redux'

const debug = false

function debounce (fn, delay) {
  let timer = null
  return function () {
    let context = this
    let args = arguments
    clearTimeout(timer)
    timer = setTimeout(function () {
      fn.apply(context, args)
    }, delay)
  }
}

const history = createBrowserHistory()

configureUrlQuery({
  history,
  addChangeHandlers: true
})

const urlPropsQueryConfig = {
  // QueryApp: { type: UrlQueryParamTypes.string, queryParam: 'app' },
  // QueryHostname: { type: UrlQueryParamTypes.string, queryParam: 'hostname' },
  // QueryLvl: { type: UrlQueryParamTypes.string, queryParam: 'lvl' },
  QueryChannel: { type: UrlQueryParamTypes.string, queryParam: 'channel' },
  QueryName: { type: UrlQueryParamTypes.string, queryParam: 'name' },
  QueryMsg: { type: UrlQueryParamTypes.string, queryParam: 'msg' }
}

class TopBar extends PureComponent {
  constructor (props) {
    super(props)
    if (debug) console.log('TopBar', props)

    this.state = {}
    this.timer = {}
    this.updateFilter = this.updateFilter.bind(this)
    // this.updateDebounceFilter = debounce(this.updateDebounceFilter, 150) // https://stackoverflow.com/a/48702173
    this.updateDebounceFilter = debounce(this.updateFilter, 150) // https://stackoverflow.com/a/48702173

    this.onWheelHandler = this.onWheelHandler.bind(this)
    this.onTouchMoveHandler = this.onTouchMoveHandler.bind(this)
    this.onTouchStartHandler = this.onTouchStartHandler.bind(this)
  }

  componentDidMount () {
    if (this.props.QueryChannel) this.props.update(this.props.QueryHostname, 'ADD_FILTER_channel')
    if (this.props.QueryName) this.props.update(this.props.QueryApp, 'ADD_FILTER_name')
    if (this.props.QueryMsg) this.props.update(this.props.QueryMsg, 'ADD_FILTER_msg')
    // if (this.props.QueryHostname) this.props.update(this.props.QueryHostname, 'ADD_FILTER_hostname')
    // if (this.props.QueryLvl) {
    //   const lvl = isNaN(this.props.QueryLvl) ? classifierLevel(this.props.QueryLvl.toUpperCase()) : this.props.QueryLvl
    //   this.props.update(lvl, 'ADD_FILTER_level')
    // }
  }

  updateFilter (userInput, type) {
    this.props.update(userInput, 'ADD_FILTER_' + type)
    switch (type) {
      case 'channel': this.props.onChangeQueryChannel(userInput); break
      case 'name': this.props.onChangeQueryName(userInput); break
      case 'msg': this.props.onChangeQueryMsg(userInput); break
      // case 'hostname': this.props.onChangeQueryHostname(userInput); break
      // case 'level': this.props.onChangeQueryLvl(userInput); break
      default:
    }
  }

  // updateDebounceFilter (userInput, type) {
  //   this.props.update(userInput, 'ADD_FILTER_' + type)
  //   switch (type) {
  //     case 'name': this.props.onChangeQueryApp(userInput); break
  //     case 'hostname': this.props.onChangeQueryHostname(userInput); break
  //     case 'level': this.props.onChangeQueryLvl(userInput); break
  //     case 'msg': this.props.onChangeQueryMsg(userInput); break
  //     default:
  //   }
  // }

  updateFilterNext (type, up) {
    if (debug) console.log('updateFilterNext', { type, up })
    switch (type) {
      // case 'hostname':
      //   const HostanameArray = Object.keys(this.props.settings.get('hostnameColor'))
      //   if (HostanameArray.length !== 1) {
      //     const index = HostanameArray.indexOf(this.props.filter.get('hostname'))
      //     let nextIndex
      //     if (up) {
      //       nextIndex = index - 1
      //       if (HostanameArray[nextIndex] === undefined) nextIndex = HostanameArray.length - 1
      //     } else {
      //       nextIndex = index + 1
      //       if (HostanameArray[nextIndex] === undefined) nextIndex = 0
      //     }
      //     this.updateFilter(HostanameArray[nextIndex], type)
      //   }
      //   break

      case 'channel':
        const ChannelArray = Object.keys(this.props.settings.get('channelColor'))
        console.log({ ChannelArray })

        if (ChannelArray.length !== 1) {
          const index = ChannelArray.indexOf(this.props.filter.get('channel'))
          let nextIndex
          if (up) {
            nextIndex = index - 1
            if (ChannelArray[nextIndex] === undefined) nextIndex = ChannelArray.length - 1
          } else {
            nextIndex = index + 1
            if (ChannelArray[nextIndex] === undefined) nextIndex = 0
          }
          this.updateFilter(ChannelArray[nextIndex], type)
        }
        break

      case 'name':
        const AppArray = Object.keys(this.props.settings.get('appColor')) // TODO: das kommt direkt aus der Line
        if (AppArray.length !== 1) {
          const index = AppArray.indexOf(this.props.filter.get('name'))
          let nextIndex
          if (up) {
            nextIndex = index - 1
            if (AppArray[nextIndex] === undefined) nextIndex = AppArray.length - 1
          } else {
            nextIndex = index + 1
            if (AppArray[nextIndex] === undefined) nextIndex = 0
          }
          this.updateFilter(AppArray[nextIndex], type)
        }
        break

        // case 'level':
        //   let level = this.props.filter.get('level')
        //   if (up) level = level - 1
        //   else level = level + 1
        //   this.updateFilter(level, type)
        //   break

      default:
        console.warn('updateFilterNext:', type)
    }
  }

  onWheelHandler ({ deltaY }, type) {
    let up
    if (deltaY > 0) up = false
    else up = true
    this.updateFilterNext(type, up)
  }

  onTouchMoveHandler ({ touches }, type) {
    if (!this.state.touchReset) {
      const t = touches[0]
      let up
      if (t.pageY - this.state.touchY > 0) up = false
      else up = true
      this.setState({ touchReset: true })
      this.updateFilterNext(type, up)
    }
  }

  onTouchStartHandler ({ touches }) {
    const t = touches[0]
    this.setState({ touchY: t.pageY, touchReset: false })
  }

  render () {
    if (debug) console.log('TopbarContainer RENDER', this.props, '############################################')

    let channel = ''
    if (this.props.filter.get('channel') !== '') {
      const appStyle = this.props.settings.get('channelColor') && this.props.settings.get('channelColor')[this.props.filter.get('channel')] ? { backgroundColor: this.props.settings.get('channelColor')[this.props.filter.get('channel')] } : {}
      channel = <Badge
        variant='secondary'
        ref='app'
        style={{ ...appStyle, margin: '0 5px 5px', touchAction: 'pan-down' }}
        onClick={() => this.updateFilter('', 'channel')}
        onWheel={(e) => this.onWheelHandler(e, 'channel')}
        onTouchMove={(e) => this.onTouchMoveHandler(e, 'channel')}
        onTouchStart={this.onTouchStartHandler}
      >{this.props.filter.get('channel')}</Badge>

      this.props.onChangeQueryChannel(this.props.filter.get('channel'))
    }

    let name = ''
    if (this.props.filter.get('name') !== '') {
      const appStyle = this.props.settings.get('appColor') && this.props.settings.get('appColor')[this.props.filter.get('name')] ? { color: this.props.settings.get('appColor')[this.props.filter.get('name')] } : {}
      name = <Badge
        variant='secondary'
        ref='app'
        style={{ ...appStyle, margin: '0 5px 5px', touchAction: 'pan-down' }}
        onClick={() => this.updateFilter('', 'name')}
        onWheel={(e) => this.onWheelHandler(e, 'name')}
        onTouchMove={(e) => this.onTouchMoveHandler(e, 'name')}
        onTouchStart={this.onTouchStartHandler}
      >{this.props.filter.get('name')}</Badge>

      this.props.onChangeQueryName(this.props.filter.get('name'))
    }

    // let lvl = ''
    // if (this.props.filter.get('level') !== '') {
    //   const level = classifierNumber(this.props.filter.get('level'))
    //   lvl = <Badge
    //     variant='secondary'
    //     className={'line'}
    //     ref='lvl'
    //     style={{ ...LevelColor[level], margin: '0 5px 5px', touchAction: 'pan-down' }}
    //     onClick={() => this.updateFilter('', 'level')}
    //     onWheel={(e) => this.onWheelHandler(e, 'level')}
    //     onTouchMove={(e) => this.onTouchMoveHandler(e, 'level')}
    //     onTouchStart={this.onTouchStartHandler}
    //     dangerouslySetInnerHTML={{ __html: level }}
    //   />

    //   this.props.onChangeQueryLvl(level)
    // }

    // let hostname = ''
    // if (this.props.filter.get('hostname') !== '') {
    //   const hostnameStyle = this.props.settings.get('hostnameColor') && this.props.settings.get('hostnameColor')[this.props.filter.get('hostname')] ? { color: this.props.settings.get('hostnameColor')[this.props.filter.get('hostname')] } : {}
    //   hostname = <Badge
    //     variant='secondary'
    //     ref='hostname'
    //     style={{ ...hostnameStyle, margin: '0 5px 5px', touchAction: 'pan-down' }}
    //     onClick={() => this.updateFilter('', 'hostname')}
    //     onWheel={(e) => this.onWheelHandler(e, 'hostname')}
    //     onTouchMove={(e) => this.onTouchMoveHandler(e, 'hostname')}
    //     onTouchStart={this.onTouchStartHandler}
    //     dangerouslySetInnerHTML={{ __html: this.props.filter.get('hostname') }}
    //   />

    //   this.props.onChangeQueryHostname(this.props.filter.get('hostname'))
    // }

    return (
      <Navbar bg='dark' variant='dark' style={{ height: '60px' }} collapseOnSelect>
        <Navbar.Brand>
          <Navbar.Text className='d-lg-none'>
            <img src={require('../logo.png')} width='30' height='30' className='d-inline-block align-top' alt='Hot Beverage' />
          </Navbar.Text>
          <Navbar.Text className='d-none d-lg-block'>
            <img src={require('../logo.png')} width='30' height='30' className='d-inline-block align-top' alt='Hot Beverage' />
            Logein
          </Navbar.Text>
        </Navbar.Brand>

        <Navbar.Text>
          <span className='d-lg-none'>{this.props.length}</span>
          <span className='d-none d-lg-block'>{this.props.length} lines</span>
        </Navbar.Text>

        <Navbar.Collapse className='justify-content-center'>
          <Navbar.Text>
            <ButtonToolbar>
              {channel}
              {/* {hostname} */}
              {name}
              {/* {lvl} */}
            </ButtonToolbar>
          </Navbar.Text>
        </Navbar.Collapse>

        <Navbar.Collapse className='justify-content-end'>
          <Form
            inline
            onSubmit={e => {
              e.preventDefault()
              this.updateFilter(this.refs.search.value, 'msg')
            }}
          >
            <FormControl
              type='text'
              placeholder='Search'
              className='mr-sm-2 btn-dark'
              ref='search'
              defaultValue={this.props.filter.get('msg')}
              onChange={() => this.updateDebounceFilter(this.refs.search.value, 'msg')}
            />
          </Form>
          <SettingsModal />
        </Navbar.Collapse>

      </Navbar>
    )
  }
}

const enhance = compose(
  connect(
    state => ({
      length: state.length,
      filter: state.filter,
      settings: state.settings
    }), // mapStateToProps
    { update } // mapDispatchToProps
  ),
  addUrlProps({ urlPropsQueryConfig })
)
export default enhance(TopBar)
