import React, { PureComponent } from 'react'

import Row from 'react-bootstrap/Row'
import Col from 'react-bootstrap/Col'

import Modal from 'react-bootstrap/Modal'
import Button from 'react-bootstrap/Button'

import Collapse from 'react-bootstrap/Collapse'

import Form from 'react-bootstrap/Form'
import FormControl from 'react-bootstrap/FormControl'
import InputGroup from 'react-bootstrap/InputGroup'
import Alert from 'react-bootstrap/Alert'

import { connect } from 'react-redux'
import { update } from '../redux'

const debug = false

class SettingsModal extends PureComponent {
  constructor (props, context) {
    super(props, context)

    this.handleShow = this.handleShow.bind(this)
    this.handleClose = this.handleClose.bind(this)
    this.handleChange = this.handleChange.bind(this)

    this.state = {
      restart: false,
      temp: '',
      addChannel: ''
    }
  }

  handleShow () {
    this.props.update(true, 'modalShow')
  }

  handleClose () {
    this.props.update(false, 'modalShow')
  }

  handleChange ({ target }) {
    this.setState({ addChannel: target.value })
  }

  render () {
    if (debug) console.log('SettingsContainer RENDER', this.props, '############################################')

    const channelColor = []
    this.props.channels.forEach((channel) => {
      channelColor.push(
        <InputGroup key={channel.name} className='mb-3'>
          <InputGroup.Prepend>
            <InputGroup.Text>{channel.name}</InputGroup.Text>
          </InputGroup.Prepend>
          <FormControl
            key={channel.name}
            type='color'
            name={channel.name}
            defaultValue={this.props.settings.get('channelColor')[channel.name]}
            onChange={({ target }) => {
              this.props.update({ [target.name]: target.value }, 'channelColor')
            }}
          />
          <InputGroup.Append>
            <Button
              variant='outline-danger'
              onClick={() => {
                let channels = this.props.channels
                const index = channels.indexOf(channel) // https://immutable-js.github.io/immutable-js/docs/#/List/indexOf
                if (index > -1) {
                  channels = channels.splice(index, 1) // https://immutable-js.github.io/immutable-js/docs/#/List/slice
                }
                this.props.update(channels, 'CHANNELS')
              }}
            >X</Button>
          </InputGroup.Append>
        </InputGroup>
      )
    })

    return (
      <>
        <Button variant='outline-secondary' onClick={this.handleShow}>
          <span role='img' aria-label='Gear'>⚙</span>
        </Button>

        <Modal
          size='lg'
          show={this.props.settings.get('modalShow')}
          onHide={this.handleClose}
          scrollable
        >
          <Modal.Header closeButton>
            <Modal.Title>Einstellungen</Modal.Title>
          </Modal.Header>
          <Modal.Body>

            <Collapse in={this.state.restart}>
              <div onClick={() => { window.location.reload() }}>
                <Alert variant='danger'>
                  Webseite Aktualisieren notwenig (F5)
                </Alert>
              </div>
            </Collapse>

            <Form.Group as={Row}>
              <Form.Label column sm='1'>Font</Form.Label>
              <Col sm='11'>
                <Form.Control
                  as='select'
                  name='fontClass'
                  value={this.props.settings.get('fontClass')}
                  onChange={({ target }) => { this.props.update(target.value, 'fontClass') }}
                >
                  <option value='Arimo'>Arimo</option>
                  <option value='Hind'>Hind</option>
                  <option value='IBM Plex Sans'>IBM Plex Sans</option>
                  <option value='Montserrat'>Montserrat</option>
                  <option value='Muli'>Muli</option>
                  <option value='Nunito Sans'>Nunito Sans</option>
                  <option value='Gothic A1'>Gothic A1</option>
                  <option value='Roboto'>Roboto</option>
                  <option value='Rubik'>Rubik</option>
                  <option value='Source Sans Pro'>Source Sans Pro</option>
                  <option value='Poppins'>Poppins</option>
                  <option value='Open Sans'>Open Sans</option>
                  <option value='Work Sans'>Work Sans</option>
                </Form.Control>
              </Col>
            </Form.Group>

            <InputGroup className='mb-3'>
              <InputGroup.Prepend>
                <InputGroup.Text id='basic-addon1'>Font Size</InputGroup.Text>
              </InputGroup.Prepend>

              <FormControl
                type='number'
                placeholder='Font Size'
                aria-label='Font Size'
                name='fontSize'
                onChange={({ target }) => { this.props.update(target.value, target.name) }}
                defaultValue={this.props.settings.get('fontSize')}
                min='6'
                max='30'
              />
              <InputGroup.Append>
                <InputGroup.Text>px</InputGroup.Text>
              </InputGroup.Append>
            </InputGroup>

            <InputGroup className='mb-3'>
              <InputGroup.Prepend>
                <InputGroup.Text>Row Height</InputGroup.Text>
              </InputGroup.Prepend>
              <FormControl
                type='number'
                placeholder='Row Height'
                aria-label='Row Height'
                name='rowHeight'
                onChange={({ target }) => {
                  this.props.update(target.value, target.name)
                  this.setState({ restart: true })
                }}
                defaultValue={this.props.settings.get('rowHeight')}
                min='3'
                max='60'
              />
              <InputGroup.Append>
                <InputGroup.Text>px</InputGroup.Text>
              </InputGroup.Append>
            </InputGroup>

            <InputGroup className='mb-3'>
              <InputGroup.Prepend>
                <InputGroup.Text>Max Lines</InputGroup.Text>
              </InputGroup.Prepend>
              <FormControl
                type='number'
                placeholder='Max Lines'
                aria-label='Max Lines'
                onChange={({ target }) => {
                  this.props.update(target.value, target.name)
                  this.setState({ restart: true })
                }}
                name='maxLines'
                defaultValue={this.props.settings.get('maxLines')}
                min='1'
              />
            </InputGroup>

            <div key='checkbox-detectLinks' className='mb-3'>
              <Form.Check
                custom
                type='checkbox'
                id='checkbox-detectLinks'
                label='Erkenne URLs/EMails/IP-Adressen und konvertiere zu einem Link'
                name='detectLinks'
                defaultChecked={this.props.settings.get('detectLinks')}
                onChange={({ target }) => { this.props.update(target.checked, target.name) }}
              />
            </div>

            <div key='checkbox-showBadges' className='mb-3'>
              <Form.Check
                custom
                type='checkbox'
                id='checkbox-showBadges'
                label='Zeige Badges an'
                name='showBadges'
                defaultChecked={this.props.settings.get('showBadges')}
                onChange={({ target }) => { this.props.update(target.checked, target.name) }}
              />
            </div>

            {channelColor}

            <InputGroup className='mb-3'>
              <FormControl // https://reactjs.org/docs/forms.html#controlled-components
                placeholder='add Channel'
                aria-label='add Channel'
                aria-describedby='basic-addon9'
                // defaultValue={this.temp}
                value={this.state.addChannel}
                onChange={this.handleChange}

                // value={this.state.addChannel}
                // onChange={({ target }) => {
                //   // this.setState({ addChannel: target.value })
                //   this.setState({ addChannel: { name: target.value, roomId: undefined } })
                // }}
                // TODO: onEnter: () =>
              />
              <InputGroup.Append>
                <Button
                  variant='outline-success'
                  onClick={() => {
                    const channels = this.props.channels.push({ name: this.state.addChannel, roomId: undefined }) // TODO: input Feld leeren
                    this.props.update(channels, 'CHANNELS') // TODO: Gleich mit neue Farbe übergeben
                    this.setState({ addChannel: '' })
                  }}
                >Add</Button>
              </InputGroup.Append>
            </InputGroup>

          </Modal.Body>
          <Modal.Footer>
            <Button variant='secondary' onClick={this.handleClose}>
              Schliessen
            </Button>
          </Modal.Footer>
        </Modal>
      </>
    )
  }
}

export default connect(
  state => ({
    settings: state.settings,
    channels: state.channels
  }), // mapStateToProps
  { update } // mapDispatchToProps
)(SettingsModal)
