import { connect } from 'react-redux'
import { update } from '../redux'

import difference from 'lodash.difference'

import TwitchJs from 'twitch-js'

const formatEmotes = (text, emotes) => {
  if (emotes === []) return text
  text = text.split('')
  for (let i = 0, len = emotes.length; i < len; i++) {
    // A cookie associated with a cross-site resource at https://jtvnw.net/ was set without the `SameSite` attribute. A future release of Chrome will only deliver cookies with cross-site requests if they are set with `SameSite=None` and `Secure`. You can review cookies in developer tools under Application>Storage>Cookies and see more details at https://www.chromestatus.com/feature/5088147346030592 and https://www.chromestatus.com/feature/5633521622188032.
    text.splice(emotes[i].start, emotes[i].end, '<img class="emoticon" src="//static-cdn.jtvnw.net/emoticons/v1/' + emotes[i].id + '/1.0">')
  }
  return text.join('')
}

let randomcolor = ['#fc5c65', '#fd9644', '#fed330', '#26de81', '#2bcbba', '#eb3b5a', '#fa8231', '#f7b731', '#20bf6b', '#0fb9b1', '#45aaf2', '#4b7bec', '#a55eea', '#d1d8e0', '#778ca3', '#2d98da', '#3867d6', '#8854d0', '#a5b1c2', '#4b6584'] // https://flatuicolors.com/
const getRandomColor = () => {
  const index = Math.floor(Math.random() * randomcolor.length)
  const color = randomcolor[index]
  randomcolor.splice(index, 1)
  if (!randomcolor.length) {
    randomcolor = ['#fc5c65', '#fd9644', '#fed330', '#26de81', '#2bcbba', '#eb3b5a', '#fa8231', '#f7b731', '#20bf6b', '#0fb9b1', '#45aaf2', '#4b7bec', '#a55eea', '#d1d8e0', '#778ca3', '#2d98da', '#3867d6', '#8854d0', '#a5b1c2', '#4b6584']
  }
  return color
}

const { chat } = new TwitchJs({ token: '', username: `justinfan${Math.floor((Math.random() * 80000) + 1000)}`, chat: { joinTimeout: 60000 } })
// const { chat } = TwitchJs.client({ token, username })
chat._log._level = -1

// eslint-disable-next-line no-control-regex
const ACTION = /(\x01ACTION )(.*)(\x01)/

// settingsStore.subscribe(async () => {
//   const all = chat._getChannels()
//   const newChannel = settingsStore.getState().channel.map(name => '#' + name)

//   const part = difference(all, newChannel)
//   await Promise.all(part.map(channel => chat.part(channel)))

//   const join = difference(newChannel, all)
//   await Promise.all(join.map(channel => chat.join(channel)))
// })

export const _disconnect = () => {
  chat.disconnect()
}

// const join = (channel) => {
//   chat.join(channel).then(data => console.log(data))
// }

const part = (channel) => {
  chat.part(channel)
}

const connected = () => chat._readyState === 3

export const _connect = ({ update, channels }) => {
  // console.log('_readyState', chat._readyState)
  // console.log(channels.toArray())
  if (connected()) { // https://github.com/twitch-devs/twitch-js/blob/083d8626c8fdfb19ff147a59f60d487b712be06c/src/Chat/index.js
    const StoreChannel = channels.toArray().map(chan => chan.name)
    // console.log('settings.get StoreChannel', StoreChannel)

    const ChannelState = Object.keys(chat._channelState).map(chan => chan.substr(1))
    // console.log({ StoreChannel, ChannelState })

    const _join = difference(StoreChannel, ChannelState)
    if (_join.length) {
      chat.join(_join[0]).then(data => {
        update({ name: _join[0], roomId: data.roomState.roomId }, 'ADD_ROOMID')
        update([{ timestamp: +new Date(), chan: _join[0], msg: '=> JOIN' }], 'ADD_DATA')
      })
    }

    const _part = difference(ChannelState, StoreChannel)
    if (_part.length) {
      console.log('part', _part)
      // part(_part.map(chan => `#${chan}`))
      part(_part[0])
    }
  } else if (chat._readyState === 0) {
    chat.connect()
      .then(async () => {
        // console.log('Connect ...')
        // await Promise.all(channels.toArray().map(channel => chat.join(channel.name))).then(data => console.log(data))
        channels.toArray().map(channel => chat.join(channel.name).then(_ => update([{ timestamp: +new Date(), chan: channel.name, msg: '=> JOIN' }], 'ADD_DATA')))
      })
      .catch(err => {
        console.warn(err)
      })

    chat.on('*', ({ channel, command, message, tags, timestamp, username }) => {
      timestamp = timestamp || +new Date()
      const chan = channel && channel.substr(1)
      const name = username
      let msg
      switch (command) {
        case 'PRIVMSG':
          msg = formatEmotes(message, tags.emotes)
          if (tags.color === null) {
            tags.color = getRandomColor()
          }
          if (msg.toLowerCase().indexOf('@' + chan) !== -1) {
            tags.attention = true
          }
          const found = ACTION.exec(msg)
          if (found !== null) {
            msg = found[2]
            tags.action = true
          }
          update([{ timestamp, chan, name, msg, tags }], 'ADD_DATA')
          break

        case 'USERNOTICE':
          update([{ timestamp, chan, msg: tags.systemMsg }], 'ADD_DATA')
          break

        case 'CLEARMSG': // https://dev.twitch.tv/docs/irc/tags#clearmsg-twitch-tags
          // Single message removal on a channel. This is triggered via /delete <target-msg-id> on IRC.
          console.log({ channel, command, message, tags, timestamp, username })
          update([{ timestamp, chan, msg: `REMOVE ${username}, ${tags.targetMsgId}` }], 'ADD_DATA')
          break

        case 'CLEARCHAT': // https://dev.twitch.tv/docs/irc/tags#clearchat-twitch-tags
        // Temporary or permanent ban on a channel.
          if (tags.banDuration) { // ban-duration (Optional) Duration of the timeout, in seconds. If omitted, the ban is permanent.
            msg = `TIMEOUT: ${username}, ${tags.banDuration + 's' + (tags.banReason ? ', ' + tags.banReason : '')}`
          } else {
            msg = `BAN: ${username}, banReason: ${tags.banReason}`
          }
          update([{ timestamp, chan, msg }], 'ADD_DATA') // TODO: tag für Farbe
          break

        case 'JOIN':
        //   update([{ timestamp, chan: '-', msg: '=> JOIN' }], 'ADD_DATA')
          break

        case 'PART':
        //   update([{ timestamp, chan: '-', msg: '<= PART' }], 'ADD_DATA')
          break

        case 'CONNECTED':
          update([{ timestamp, chan: 'twitch.tv', msg: 'CONNECTED' }], 'ADD_DATA')
          break

          // "HOSTTARGET"
          // { channel: "#tsm_viss", command: "HOSTTARGET", message: undefined, tags: { … }, timestamp: Sat Nov 02 2019 00: 00: 23 GMT + 0100(Mitteleuropäische Normalzeit), … }
          // channel: "#tsm_viss"
          // command: "HOSTTARGET"
          // message: undefined
          // tags: { }
          // timestamp: Sat Nov 02 2019 00: 00: 23 GMT + 0100(Mitteleuropäische Normalzeit) { }
          // username: "halifax"

          // "NOTICE"
          // { channel: "#tsm_viss", command: "NOTICE", message: "Now hosting Halifax.", tags: { … }, timestamp: Sat Nov 02 2019 00: 00: 23 GMT + 0100(Mitteleuropäische Normalzeit), … }
          // channel: "#tsm_viss"
          // command: "NOTICE"
          // message: "Now hosting Halifax."
          // tags: { msgId: "host_on" }
          // timestamp: Sat Nov 02 2019 00: 00: 23 GMT + 0100(Mitteleuropäische Normalzeit) { }
          // username: "tmi.twitch.tv"

        case '001': ; break
        case '002': ; break
        case '003': ; break
        case '004': ; break
        case '353': ; break
        case '366': ; break
        case '372': ; break
        case '375': ; break
        case '376': ; break
        case 'CAP': ; break
        case 'MODE': ; break
        case 'PING': ; break
        case 'ROOMSTATE': ; break

        case 'PARSE_ERROR_ENCOUNTERED': ; break

        default:
          console.log('default', { channel, command, message, tags, timestamp, username })
      }
    })
  }

  return null
}

export default connect(
  state => ({
    channels: state.channels
  }), // mapStateToProps
  // state => ({ settings: state.settings }), // mapStateToProps
  // state => ({ }), // mapStateToProps
  { update } // mapDispatchToProps
)(_connect)
