import { CellMeasurerCache } from 'react-virtualized'

export const cache = new CellMeasurerCache({ // https://github.com/bvaughn/react-virtualized/blob/master/docs/CellMeasurer.md#cellmeasurercache
  minHeight: 19,
  defaultHeight: 19,
  fixedWidth: true,
  keyMapper: rowIndex => rowIndex
})

export const refreshCache = function (rowHeight = 19) {
  // console.log('refreshCache()')
  // console.trace()

  cache._minHeight = rowHeight
  cache._defaultHeight = rowHeight
  cache.clearAll()
}
